import './login.css';
import React, { Component } from 'react';

import history from '../../../../Routing/history';

class HackableLogin extends Component {
    state = {  }
    render() { 
        return ( <React.Fragment>
            <div className="hackable-login-body-bg">
                <div className="container-fluid">
                    <div className="row mt-5">
                        <div className="col-12 hackable-login-title">
                            Login
                        </div>
                    </div>
                    <div className="row">
                        <div className="col-12 hackable-login-sub-title">
                            Welcome back! Please login to continue
                        </div>
                    </div>
                    <div className="row mt-5">
                        <div className="col-12">
                            <div class="input-group mx-auto w-75">
                                <div class="input-group-prepend">
                                    <span class="input-group-text bg-white border-right-0">
                                        <img
                                            alt="mail"
                                            className="hackable-login-input-icon"
                                            src={require('../../../Assets/Icons/HackableModule/mail.svg')}
                                        />
                                    </span>
                                </div>
                                <input type="email" class="form-control border-left-0" placeholder="Email"/>
                            </div>
                        </div>
                    </div>
                    <div className="row mt-4">
                        <div className="col-12">
                            <div class="input-group mx-auto w-75">
                                <div class="input-group-prepend">
                                    <span class="input-group-text bg-white border-right-0">
                                        <img
                                            alt="mail"
                                            className="hackable-login-input-icon"
                                            src={require('../../../Assets/Icons/HackableModule/lock.svg')}
                                        />
                                    </span>
                                </div>
                                <input type="email" class="form-control border-left-0" placeholder="Email"/>
                            </div>
                        </div>
                    </div>
                    <div className="row mt-5">
                        <div className="col-12 text-center">
                            <button onClick={()=>history.push('/Hackable_Post')} className="btn hackable-login-btn">
                                Sign in
                            </button>
                        </div>
                    </div>
                </div>
            </div>
        </React.Fragment> );
    }
}
 
export default HackableLogin;