import "./signupView.css";

import React, { Component } from "react";

import Signup from "../../Components/SignUp/shignUp";

class SignupView extends Component {
  state = {};
  render() {
    return (
      <React.Fragment>
        <div className="container-fluid sign-up-view-bg">
          <div className="row">
            <div className="col-12">
              <Signup />
            </div>
          </div>
        </div>
      </React.Fragment>
    );
  }
}

export default SignupView;
