import './home.css';
import React, { Component } from 'react';

class Card extends Component {
    state = {  }
    render() { 
        return ( <React.Fragment>
            <div className="home-card-box">
                <div className="container-fluid">
                    <div className="row">
                        <img
                            alt="card"
                            className="home-card-img"
                            src={this.props.src}
                        />
                    </div>
                    <div className="row mt-2">
                        <dov className="col-12 home-card-author">
                            {this.props.author}
                        </dov>
                    </div>
                    <div className="row mt-2">
                        <dov className="col-12 home-card-title">
                            {this.props.title}
                        </dov>
                    </div>
                    <div style={{position:"absolute", bottom:"25px"}} className="row pb-2">
                        <div className="col-12">
                            <div className="row">
                                <div className="col-12 p-0">
                                    <img
                                        alt="calender"
                                        className="home-card-calender-icon"
                                        src={require('../../Assets/Icons/Home/calendar.svg')}
                                    />
                                    <div className="home-card-calender">
                                        Feburary&nbsp;19,&nbsp;2020&nbsp;10:26
                                    </div> 
                                </div>
                            </div>
                            <div className="row">
                                <div className="col-12 p-0">
                                    <img
                                        alt="calender"
                                        className="home-card-calender-icon"
                                        src={require('../../Assets/Icons/Home/clock.svg')}
                                    />
                                    <div className="home-card-calender">
                                        3&nbsp;min&nbsp;read
                                    </div> 
                                    <img
                                        alt="calender"
                                        className="home-card-calender-icon"
                                        src={require('../../Assets/Icons/Home/eye.svg')}
                                    />
                                    <div className="home-card-calender">
                                        100&nbsp;views&nbsp;so&nbsp;far
                                    </div> 
                                </div>
                            </div>
                            <div className="row">
                                <div className="col-12 pl-1">
                                    <button className="btn home-post-body-post-btn">
                                        Read More
                                    </button>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </React.Fragment> );
    }
}
 
export default Card;