import './header.css';
import React, { Component } from 'react';

import history from '../../../../Routing/history';

class HackableHeader extends Component {
    state = {  }
    render() { 
        return ( <React.Fragment>
            <div className="container-fluid hackable-header-bg">
                <div className="row">
                    <div onClick={()=>history.push('/')} className="col-6 hackable-header-logo pl-5 cursor-pointer">
                        Logo
                    </div>
                    <div onClick={()=>history.push('/Hackable_login')} className="col-6 hackable-header-login pr-5 cursor-pointer">
                        Login
                    </div>
                </div>
            </div>
        </React.Fragment> );
    }
}
 
export default HackableHeader;