import '../../../Assets/Styles/common.css';
import './editBlogView.css';
import React, { Component } from 'react';

import Navbar from '../../../Components/BlogAction/Navbar/navbar';
import Sidebar from '../../../Components/BlogAction/Sidebar/sidebar';
import EditBlog from '../../../Components/BlogAction/EditBlog/editBlog';

class EditBlogView extends Component {

    render() { 
        return ( <React.Fragment>
            <div className="blog-action-view-bg">
                <div 
                    className={
                        !this.props.collpase?
                        "blog-action-view-sidebar-open"
                        :
                        "blog-action-view-sidebar-close"
                    }
                >
                    <Sidebar
                        collpase={this.props.collpase}
                        selected={1}
                    />
                </div>
                <div 
                    className={
                        !this.props.collpase?
                        "blog-action-view-body-open"
                        :
                        "blog-action-view-body-close"
                    }
                >
                    <div className="view-body-scroll">
                        <div className="blog-action-view-blue-bg">
                            <div className="container-fluid">
                                <div className="row">
                                    <div className="col-12">
                                        <Navbar
                                            handleCollpase={this.props.handleCollpase}
                                            title="Blogs"
                                        />
                                    </div>
                                </div>
                                <div className="row mt-4">
                                    <div className="col-0 col-sm-0 col-md-0 col-lg-1 col-xl-1"/>
                                    <div className="col-12 col-sm-12 col-md-12 col-lg-10 col-xl-10 pl-4 pr-4">
                                        <div className="row">
                                            <div style={{padding:"0 30px 0 0"}} className="col-12 text-right">
                                                <button className="btn blog-action-edit-blog-view-edit-btn">
                                                    <img 
                                                        alt="edit"
                                                        className="blog-action-edit-blog-view-edit-icon"
                                                        src={require('../../../Assets/Icons/BlogAction/edit.svg')}
                                                    />
                                                    EDIT
                                                </button>
                                            </div>
                                        </div>
                                        <div className="row mt-3">
                                            <div className="col-12">
                                                <EditBlog/>
                                            </div>
                                        </div>
                                    </div>
                                    <div className="col-12 col-sm-0 col-md-0 col-lg-1 col-xl-1"/>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </React.Fragment> );
    }
}
 
export default EditBlogView;