import './postView.css';
import React, { Component } from 'react';

import Header from '../../../Components/HackableModule/Header/header';
import CreatePost from '../../../Components/HackableModule/Post/createPost';
import PostCard from '../../../Components/HackableModule/Post/postCard';

class PostView extends Component {
    state = {  }
    render() { 
        return ( <React.Fragment>
            <div className="container-fluid hackable-post-view-bg">
                <div className="row">
                    <div className="w-100">
                        <Header/>
                    </div>
                </div>
                <div className="row">
                    <div className="col-0 col-sm-0 col-md-0 col-lg-1 col-xl-2"/>
                    <div className="col-12 col-sm-12 col-md-12 col-lg-10 col-xl-8">
                        <div className="row mt-5">
                            <div className="col-12">
                                <CreatePost/>
                            </div>
                        </div>
                        <div className="row mt-5">
                            <div style={{fontSize:"25px"}} className="col-12">
                                Posts
                            </div>
                        </div>
                        <div className="row mt-5">
                            <div className="col-12">
                                <PostCard/>
                            </div>
                        </div>
                    </div>
                    <div className="col-0 col-sm-0 col-md-0 col-lg-1 col-xl-2"/>
                </div>
            </div>
        </React.Fragment> );
    }
}
 
export default PostView;