import './categoryList.css';
import React, { Component } from 'react';
import history from '../../../Routing/history';

class CategoryListTable extends Component {
    state = { 
        searchSelect: false
     }
     handleSearchSelect = () =>{
         this.setState({
             searchSelect: !this.state.searchSelect
         })
     }
    render() { 
        return ( <React.Fragment>
            <div className="container-fluid bg-white rounded-lg shadow-sm">
                <div className="row pt-2">
                    <div className="col-12 col-sm-12 col-md-5 col-lg-6 col-xl-6 admin-category-list-title">
                        Categories List
                    </div>
                    <div className="col-12 col-sm-12 col-md-7 col-lg-6 col-xl-6">
                        <button 
                            data-toggle="modal" data-target="#addCategoryModal"
                            className="btn admin-category-list-add-blog-btn"
                        >
                            <img
                                alt="blog"
                                className="admin-category-list-add-blog-icon"
                                src={require('../../../Assets/Icons/BlogAction/blog.svg')}
                            />
                            Add Category
                        </button>
                        {
                            this.state.searchSelect?
                            <div class="input-group admin-category-list-search-input-group mb-3">
                              <input type="text" class="form-control admin-category-list-search-input" placeholder="Search"/>
                              <div class="input-group-prepend">
                                <span onClick={()=>this.handleSearchSelect()} class="input-group-text admin-category-list-search-append">X</span>
                              </div>
                            </div>
                            :
                            <img
                                alt="search"
                                onClick={()=>this.handleSearchSelect()}
                                className="admin-category-list-search-icon"
                                src={require('../../../Assets/Icons/BlogAction/search.svg')}
                            />
                        }
                    </div>
                </div>
                <div className="row">
                    <div className="col-12 p-0">
                        <table className="table table-striped table-responsive-sm">
                            <thead className="bg-light">
                                <td className="text-center text-muted">Sr#</td>
                                <td className="text-center text-muted">Name</td>
                                <td className="text-center text-muted">Action</td>
                            </thead>
                            <tbody>
                                <tr>
                                    <td className="text-center">Blog-0001</td>
                                    <td className="text-center pl-5 pr-5">John Vick</td>
                                    <td className="text-center">
                                        <div className="admin-category-list-table-action-box">
                                            <img
                                                alt="edit"
                                                className="admin-category-list-table-action-edit-icon"
                                                src={require('../../Assets/Icons/Category/edit.svg')}
                                            />
                                        </div>
                                    </td>
                                </tr>
                                <tr>
                                    <td className="text-center">Blog-0001</td>
                                    <td className="text-center pl-5 pr-5">John Vick</td>
                                    <td className="text-center">
                                        <div className="admin-category-list-table-action-box">
                                            <img
                                                alt="edit"
                                                className="admin-category-list-table-action-edit-icon"
                                                src={require('../../Assets/Icons/Category/edit.svg')}
                                            />
                                        </div>
                                    </td>
                                </tr>
                                <tr>
                                    <td className="text-center">Blog-0001</td>
                                    <td className="text-center pl-5 pr-5">John Vick</td>
                                    <td className="text-center">
                                        <div className="admin-category-list-table-action-box">
                                            <img
                                                alt="edit"
                                                className="admin-category-list-table-action-edit-icon"
                                                src={require('../../Assets/Icons/Category/edit.svg')}
                                            />
                                        </div>
                                    </td>
                                </tr>
                            </tbody>
                        </table>
                    </div>
                </div>
            </div>
            <div class="modal fade" id="addCategoryModal" tabindex="-1" role="dialog" aria-labelledby="addCategoryModalTitle" aria-hidden="true">
                <div class="modal-dialog modal-dialog-centered" role="document">
                    <div class="modal-content">
                        <div class="modal-header border-bottom-0">
                            <h5 class="modal-title w-100 text-center" id="addCategoryModalTitle">Add Category</h5>
                            <div data-dismiss="modal" aria-label="Close" className="admin-category-list-modal-close">
                                X
                            </div>
                        </div>
                        <div class="modal-body">
                            <div className="container-fluid">
                                <div className="row">
                                    <div className="col-12 text-center pr-5 pl-5">
                                        <div class="input-group mb-3">
                                            <div class="input-group-prepend">
                                                <span class="input-group-text admin-category-list-modal-input-icon">
                                                    <img
                                                        alt="dashboard"
                                                        style={{width:"20px"}}
                                                        src={require('../../Assets/Icons/Category/dashboard.svg')}
                                                    />
                                                </span>
                                            </div>
                                            <input type="text" class="form-control admin-category-list-modal-input" placeholder="Name of Category"/>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                        <div class="modal-footer border-top-0">
                            {/* <button type="button" class="btn btn-secondary" data-dismiss="modal">Close</button> */}
                            <button type="button" class="btn admin-category-list-modal-add-btn">ADD</button>
                        </div>
                    </div>
                </div>
            </div>
        </React.Fragment> );
    }
}
 
export default CategoryListTable;