import './dashboard.css';
import React, { Component } from 'react';

class Card extends Component {
    state = {  }
    render() { 
        return ( <React.Fragment>
            <div className="admin-dashboard-body-card-box shadow-sm">
                <div className="container-fluid">
                    <div className="row">
                        <div className="col-8">
                            <div className="row">
                                <div className="col-12 admin-dashboard-body-card-title">
                                    {this.props.title}
                                </div>
                            </div>
                            <div className="row">
                                <div className="col-12 admin-dashboard-body-card-amount">
                                    {this.props.amount}
                                </div>
                            </div>
                            <div className="row">
                                <div className="col-12 admin-dashboard-body-card-interval">
                                    {this.props.interval}
                                </div>
                            </div>
                        </div>
                        <div className="col-4">
                            <div 
                                className={
                                    this.props.title === "TOTAL USERS"?
                                    "admin-dashboard-body-card-icon-box admin-dashboard-body-card-icon-box-total-user"
                                    :this.props.title === "NEW USERS"?
                                    "admin-dashboard-body-card-icon-box admin-dashboard-body-card-icon-box-new-user"
                                    :this.props.title === "TOTAL BLOGS"?
                                    "admin-dashboard-body-card-icon-box admin-dashboard-body-card-icon-box-total-blog"
                                    :this.props.title === "TOTAL GUIDES"?
                                    "admin-dashboard-body-card-icon-box admin-dashboard-body-card-icon-box-total-guide"
                                    :""
                                }
                            >
                                <img
                                    alt="card"
                                    className="admin-dashboard-body-card-icon"
                                    src={this.props.src}
                                />
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </React.Fragment> );
    }
}
 
export default Card;