import './dashboard.css';

import React, { Component } from 'react';
import { Bar } from 'react-chartjs-2';

const options={
    legend: {
        display: false,
    },
};

class BarChart extends Component {
    state = { 
        data : {
            labels: ['Jan', 'Febr', 'Mar', 'April', 'May', 'Jun', 'Jul', 'Aug', 'Sep', 'Oct', 'Nov', 'Dec'],
            datasets:[
                {
                    label: "Total Orders",
                    fill: false,
                    lineTension: 0.5,
                    backgroundColor: '#FB6340',
                    borderColor: 'rgba(75,192,192,1)',
                    borderCapStyle: 'butt',
                    borderDash: [],
                    borderDashOffset: 0.0,
                    borderJoinStyle: 'miter',
                    pointBorderColor: 'rgba(75,192,192,1)',
                    pointBackgroundColor: '#fff',
                    pointBorderWidth: 0.1,
                    pointHoverRadius: 1,
                    pointHoverBackgroundColor: 'rgba(75,192,192,1)',
                    pointHoverBorderColor: 'rgba(220,220,220,1)',
                    pointHoverBorderWidth: 1,
                    pointRadius: 1,
                    pointHitRadius: 1,
                    barPercentage: 0.4,
                    data: [3, 20, 10, 30, 20, 40, 30, 50, 30, 60, 70, 80]
                }
            ]
        }
     }
    render() { 
        return ( <React.Fragment>
            <div style={{backgroundColor:"#ffffff"}} className="container-fluid rounded shadow-sm">
                <div className="row">
                    <div className="col-12">
                        <div className="row">
                            <div className="col-12 admin-dashboard-card-title">
                                OVERVIEW
                            </div>
                        </div>
                        <div className="row">
                            <div className="col-12 admin-dashboard-card-sub-title">
                                Total Blogs
                            </div>
                        </div>
                    </div>
                </div>
                <div className="row">
                    <div className="col-12">
                        <Bar
                            height={235}
                            options={options}
                            data={this.state.data}
                        />
                    </div>
                </div>
            </div>
        </React.Fragment> );
    }
}
 
export default BarChart;