import '../../Assets/Styles/common.css'
import "./signIn.css";
import React, { Component } from 'react';
import history from '../../../Routing/history';
import ColmnImage from "../../Assets/Icons/SignIn/NoPath - Copy (11).png";
import LockIcon from "../../Assets/Icons/SignIn/Icon awesome-lock.png";
import EmailIcon from "../../Assets/Icons/SignIn/Icon ionic-ios-mail.png";
class SignIn extends Component {
    state = {  }
    render() { 
        return ( 
            <React.Fragment>
                <div className="container-fluid sign-in-container bg-white  h-100">
                    <div className="row ">
                        <div className="col-xl-7 col-lg-6 col-md-d-none col-sm-none col-none mt-5 Image-colomn-padding">
                            <img 
                                // style={{position:"relative",top:"0",bottom:"0",left:"0",right:"0",margin:"auto"}} 
                                className="sign-in-cover-image"
                                src={ColmnImage} 
                                width="40%"
                                />
                        </div>
                        <div className="col-xl-5 col-lg-6 col-md-12 col-sm-12 col-12 p-5">
                        <div className="row  pt-5 pb-4">
                        <div className="col-xl-12 Sign-In-heading">
                            Sign In
                        </div>
                    </div>
                    <div class="Sign-in-input-container-position sign-in-input-container">
                        <img src={EmailIcon} className="mt-2"/>
                        <input type="text" class="Sign-IN-input-fields" placeholder="Email" />
                        </div>
                        <div class="Sign-in-input-container-position sign-in-input-container">
                        <img src={LockIcon}/>
                        <input type="text" class="Sign-IN-input-fields" placeholder="Password" />
                        </div>
                        <div className="row">
                            <div onClick={()=>history.push('/Forgrt/Enter_Email')} className="col-12 cursor-pointer text-left">Forget Password?</div>
                        </div>
                        <div className="row mt-4">
                        <div className="col-xl-12">
                        <label class="sign-in-page-checkbox-container"><span className="Sign-in-keep-text">Keep me Sign in</span>
                        <input type="checkbox" />
                        <span class="new-created-check-box-sign"></span>
                        </label>
                        </div>
                        </div>
                        <div className="text-left">
                        <button 
                            onClick={()=>history.push('/Blog_List')} 
                            className="sign-in-Register-button"
                        >
                            Sign in
                        </button>
                        <button 
                            // onClick={()=>history.push('/Blog_List')} 
                            className="sign-in-Register-button"
                        >
                            <img
                                alt="fb"
                                style={{width:"13px", margin:"-3px 5px 0 0"}}
                                src={require('../../Assets/Icons/SignIn/facebook.svg')}
                            />
                            Facebook
                        </button>
                        <button 
                            // onClick={()=>history.push('/Blog_List')} 
                            className="sign-in-Register-button"
                        >
                        <img
                            alt="fb"
                            style={{width:"13px", margin:"-3px 5px 0 0"}}
                            src={require('../../Assets/Icons/SignIn/google.svg')}
                        />
                            Google
                        </button>
                        </div>
                        
                        </div>

                    </div>
                    <div className="row">
                    <div className="col-xl-7 col-lg-6 col-md-none col-sm-none col-none">

                    </div>
                    <div className="col-xl-5 col-lg-6 col-md-12 col-sm-12 col-12 mt-n5 pl-n5 text-center">
                    <p onClick={()=>history.push('/Signup')} className="text-center ml-n5"><a className="not-have-an-account-text">Not have an account signUp?</a></p>
                    </div>
                    </div>
                </div>
            </React.Fragment>
         );
    }
}
 
export default SignIn;