import './home.css';
import React, { Component } from 'react';

class Mail extends Component {
    state = {  }
    render() { 
        return ( <React.Fragment>
            <div className="container-fluid pt-3 pb-3 home-mail-bg">
                <div className="row">
                    <div className="col-1"/>
                    <div className="col-10">
                        <div className="row">
                            <div className="col-12 home-mail-title">
                                JOIN OVER 20,000
                                <br/>
                                SUBSCRIBER
                            </div>
                        </div>
                        <div className="row">
                            <div className="col-12 text-center">
                                <img
                                    alt="mail"
                                    className="home-mail-icon"
                                    src={require('../../Assets/Icons/Home/envelope.svg')}
                                />
                            </div>
                        </div>
                        <div className="row">
                            <div className="col-12 home-mail-title">
                                Click here to receive
                                <br/>
                                email updates
                            </div>
                        </div>
                    </div>
                    <div className="col-1"/>
                </div>
            </div>
        </React.Fragment> );
    }
}
 
export default Mail;