import "./navbar.css";
import React, { Component } from "react";
import history from "../../../Routing/history";

class Navbar extends Component {
  state = {};
  render() {
    return (
      <React.Fragment>
        <nav class="navbar navbar-expand-lg bg-white fixed-top">
          <a onClick={() => history.push("/security-system/")} class="nav-logo">
            Logo
          </a>
          <button
            class="navbar-toggler"
            type="button"
            data-toggle="collapse"
            data-target="#navbarSupportedContent"
            aria-controls="navbarSupportedContent"
            aria-expanded="false"
            aria-label="Toggle navigation"
          >
            <span class="navbar-toggler-icon"></span>
          </button>

          <div class="collapse navbar-collapse" id="navbarSupportedContent">
            <ul class="navbar-nav ml-auto">
              <li class="nav-item dropdown nav-option-container">
                <a
                  class="nav-link dropdown-toggle nav-option-text"
                  id="navbarDropdown"
                  role="button"
                  data-toggle="dropdown"
                  aria-haspopup="true"
                  aria-expanded="false"
                >
                  Blog
                </a>
                <div
                  class="dropdown-menu text-center"
                  aria-labelledby="navbarDropdown"
                >
                  <a
                    onClick={() => history.push("/Blog")}
                    class="dropdown-item cursor-pointer"
                  >
                    Blogs
                  </a>
                  <a
                    onClick={() => history.push("/Blog_List")}
                    class="dropdown-item cursor-pointer"
                  >
                    Add Blog
                  </a>
                  {/* <a class="dropdown-item" href="#">Another action</a>
                                <div class="dropdown-divider"></div>
                                <a class="dropdown-item" href="#">Something else here</a> */}
                </div>
              </li>
              <li class="nav-item nav-option-container">
                <a
                  onClick={() => history.push("/Guide")}
                  class="nav-link nav-option-text"
                >
                  Guides
                </a>
              </li>
              <li class="nav-item nav-option-container">
                <a
                  onClick={() => history.push("/Hackable_login")}
                  class="nav-link nav-option-text"
                >
                  Hackable Module
                </a>
              </li>
              <li class="nav-item nav-option-container">
                <a
                  data-toggle="modal"
                  data-target="#contactModal"
                  class="nav-link nav-option-text"
                >
                  Contact Us
                </a>
              </li>
              <li class="nav-item nav-option-container">
                <div className="nav-auth-container">
                  <div
                    onClick={() => history.push("/Login")}
                    className="nav-auth-login"
                  >
                    Login
                  </div>
                  <div
                    onClick={() => history.push("/Signup")}
                    className="nav-auth-signup"
                  >
                    Signup
                  </div>
                </div>
              </li>
            </ul>
          </div>
        </nav>
        <div
          class="modal fade"
          id="contactModal"
          tabindex="-1"
          role="dialog"
          aria-labelledby="contactModalTitle"
          aria-hidden="true"
        >
          <div class="modal-dialog modal-dialog-centered" role="document">
            <div class="modal-content">
              <div class="modal-header border-bottom-0">
                <h5
                  class="modal-title w-100 text-center"
                  id="contactModalTitle"
                >
                  Contact Us
                </h5>
                <div
                  data-dismiss="modal"
                  aria-label="Close"
                  className="admin-category-list-modal-close"
                >
                  X
                </div>
              </div>
              <div class="modal-body">
                <div className="container-fluid">
                  <div className="row">
                    <div className="col-12 text-center pr-5 pl-5">
                      <div class="input-group mb-3">
                        <div class="input-group-prepend">
                          <span class="input-group-text admin-category-list-modal-input-icon">
                            <img
                              alt="dashboard"
                              style={{ width: "20px" }}
                              src={require("../../Assets/Icons/SignIn/Icon ionic-ios-mail.png")}
                            />
                          </span>
                        </div>
                        <input
                          type="text"
                          class="form-control admin-category-list-modal-input"
                          placeholder="Email"
                        />
                      </div>
                    </div>
                  </div>
                  <div className="row">
                    <div className="col-12 text-center pr-5 pl-5">
                      <textarea
                        placeholder="Enter Message"
                        class="form-control admin-category-list-modal-input"
                        rows="5"
                      />
                      {/* <div class="input-group mb-3">
                                            <div class="input-group-prepend">
                                                <span class="input-group-text admin-category-list-modal-input-icon">
                                                    <img
                                                        alt="dashboard"
                                                        style={{width:"20px"}}
                                                        src={require('../../Assets/Icons/SignIn/Icon ionic-ios-mail.png')}
                                                    />
                                                </span>
                                            </div>
                                            <input type="text" class="form-control admin-category-list-modal-input" placeholder="Enter Message"/>
                                        </div> */}
                    </div>
                  </div>
                </div>
              </div>
              <div class="modal-footer border-top-0 pb-5">
                {/* <button type="button" class="btn btn-secondary" data-dismiss="modal">Close</button> */}
                <button type="button" class="btn nav-modal-modal-submit-btn">
                  SUBMIT
                </button>
              </div>
            </div>
          </div>
        </div>
      </React.Fragment>
    );
  }
}

export default Navbar;
