import "./guide.css";
import React, { Component } from "react";

import history from "../../../Routing/history";

class Card extends Component {
  state = {};
  render() {
    return (
      <React.Fragment>
        <div className="guide-body-card-box">
          <div className="container-fluid">
            <div className="row">
              <div className="col-12">
                <img
                  onClick={() => history.push("/Guide_Description")}
                  alt="card"
                  className="guide-body-card-img"
                  src={this.props.src}
                />
              </div>
            </div>
            <div className="row">
              <div className="col-12 guide-body-card-title">
                {this.props.title}
              </div>
            </div>
            <div className="row">
              <div className="col-12 guide-body-card-text">
                {this.props.description}
              </div>
            </div>
            <div
              style={{ position: "absolute", bottom: "30px" }}
              className="row"
            >
              <div className="col-12">
                <div className="row">
                  <div className="col-12 guide-body-card-date">
                    Feburary 19, 2020 10:26
                  </div>
                </div>
                <div className="row mt-3">
                  <div className="col-12">
                    <div className="guide-body-card-info-text">3 min read</div>
                    <img
                      alt="eye"
                      className="guide-body-card-info-icon"
                      src={require("../../Assets/Icons/Guide/eye.svg")}
                    />
                    <div className="guide-body-card-info-text">
                      100 views so far
                    </div>
                  </div>
                </div>
                <div className="row mt-2">
                  <div className="col-12 text-left">
                    <button
                      onClick={() => history.push("/Guide_Description")}
                      className="btn guide-body-card-btn"
                    >
                      Read More
                    </button>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </React.Fragment>
    );
  }
}

export default Card;
