import './dashboard.css';

import React, { Component } from 'react';
import { Line } from 'react-chartjs-2';

const options={
    legend: {
        display: false,
    },
};

class LineChart extends Component {
    state = { 
        click:"MONTH",
        month : {
            labels: ['Jan', 'Febr', 'Mar', 'April', 'May', 'Jun', 'Jul', 'Aug', 'Sep', 'Oct', 'Nov', 'Dec'],
            datasets: [
              {
                // label: 'Sales Value',
                fill: false,
                lineTension: 0.5,
                backgroundColor: 'rgba(75,192,192,0.4)',
                borderColor: 'rgba(75,192,192,1)',
                borderCapStyle: 'butt',
                borderDash: [],
                borderDashOffset: 0.0,
                borderJoinStyle: 'miter',
                pointBorderColor: 'rgba(75,192,192,1)',
                pointBackgroundColor: '#fff',
                pointBorderWidth: 1,
                pointHoverRadius: 5,
                pointHoverBackgroundColor: 'rgba(75,192,192,1)',
                pointHoverBorderColor: 'rgba(220,220,220,1)',
                pointHoverBorderWidth: 2,
                pointRadius: 1,
                pointHitRadius: 10,
                data: [0, 20, 10, 30, 20, 40, 30, 50, 30, 60, 70, 80]
              }
            ]
          },
          week : {
            labels: ['Mon', 'Tue', 'Wed', 'Thu', 'Fri', 'Sat', 'Sun'],
            datasets: [
              {
                // label: 'Sales Value',
                fill: false,
                lineTension: 0.5,
                backgroundColor: 'rgba(75,192,192,0.4)',
                borderColor: 'rgba(75,192,192,1)',
                borderCapStyle: 'butt',
                borderDash: [],
                borderDashOffset: 0.0,
                borderJoinStyle: 'miter',
                pointBorderColor: 'rgba(75,192,192,1)',
                pointBackgroundColor: '#fff',
                pointBorderWidth: 1,
                pointHoverRadius: 5,
                pointHoverBackgroundColor: 'rgba(75,192,192,1)',
                pointHoverBorderColor: 'rgba(220,220,220,1)',
                pointHoverBorderWidth: 2,
                pointRadius: 1,
                pointHitRadius: 10,
                data: [0, 20, 10, 30, 20, 40, 70]
              }
            ]
          },
          legend: {
            display: false
          }
     }
     handleClick = (CLICK) =>{
         this.setState({
             click: CLICK
         });
     }
    render() { 
        return ( <React.Fragment>
            <div style={{backgroundColor:"#ffffff"}} className="container-fluid rounded shadow-sm">
                <div className="row">
                    <div className="col">
                        <div className="row">
                            <div className="col-12 admin-dashboard-card-title">
                                OVERVIEW
                            </div>
                        </div>
                        <div className="row">
                            <div className="col-12 admin-dashboard-card-sub-title">
                                People Visit site this month
                            </div>
                        </div>
                    </div>
                    <div className="col pt-3">
                        <button 
                            className={
                                this.state.click === "WEEK"?
                                "btn admin-dashboard-chart-line-btn admin-dashboard-chart-line-btn-active shadow-sm"
                                :"btn admin-dashboard-chart-line-btn shadow-sm"
                            }
                            onClick={()=>this.handleClick("WEEK")}
                        >
                            Week
                        </button>
                        <button 
                            className={
                                this.state.click === "MONTH"?
                                "btn admin-dashboard-chart-line-btn admin-dashboard-chart-line-btn-active shadow-sm"
                                :"btn admin-dashboard-chart-line-btn shadow-sm"
                            }
                            onClick={()=>this.handleClick("MONTH")}
                        >
                            Month
                        </button>
                    </div>
                </div>
                <div className="row">
                    <div className="col-12">
                        <Line
                            ref="chart"
                            options={options}
                            data={
                                this.state.click === "MONTH"?
                                this.state.month
                                :this.state.click === "WEEK"?
                                this.state.week
                                :""
                            }
                        />
                    </div>
                </div>
            </div>
        </React.Fragment> );
    }
}
 
export default LineChart;