import './sidebar.css';
import React, { Component } from 'react';

import history from '../../../Routing/history'

class Sidebar extends Component {
    state = { 
        selected: this.props.selected? this.props.selected : 1
     }

     handleSelected = (SELECT) =>{
         this.setState({
             selected: SELECT
         });
     };
    render() { 
        return ( <React.Fragment>
            <div className="container-fluid p-0">
                <div className="row mt-5">
                    <div className="col-12">
                        <div 
                            onClick={()=>{
                                this.handleSelected(1);
                                history.push('/Admin_Dashboard');
                            }}
                            className={
                                this.state.selected === 1?
                                "admin-sidebar-option-active"
                                :"admin-sidebar-option"
                            }
                        >
                            <img
                                alt="blog"
                                className="admin-sidebar-option-icon"
                                src={require('../../Assets/Icons/BlogAction/dashboard.svg')} 
                            />
                            <div className="admin-sidebar-option-text">
                                {
                                    !this.props.collpase?
                                    "DASHBOARD"
                                    :
                                    ""
                                }
                            </div>
                        </div>
                    </div>
                </div>
                <div className="row mt-4">
                    <div className="col-12">
                        <div 
                            onClick={()=>{
                                this.handleSelected(2);
                                history.push('/Admin_Requested_Blog');
                            }}
                            className={
                                this.state.selected === 2?
                                "admin-sidebar-option-active"
                                :"admin-sidebar-option"
                            }
                        >
                            <img
                                alt="blog"
                                className="admin-sidebar-option-icon"
                                src={require('../../Assets/Icons/BlogAction/blog.svg')} 
                            />
                            <div className="admin-sidebar-option-text">
                                {
                                    !this.props.collpase?
                                    "Requested Blogs"
                                    :
                                    ""
                                }
                            </div>
                        </div>
                    </div>
                </div>
                <div className="row mt-4">
                    <div className="col-12">
                        <div 
                            onClick={()=>{
                                this.handleSelected(3);
                                history.push('/Admin_Guide');
                            }}
                            className={
                                this.state.selected === 3?
                                "admin-sidebar-option-active"
                                :"admin-sidebar-option"
                            }
                        >
                            <img
                                alt="blog"
                                className="admin-sidebar-option-icon"
                                src={require('../../Assets/Icons/BlogAction/blog.svg')} 
                            />
                            <div className="admin-sidebar-option-text">
                                {
                                    !this.props.collpase?
                                    "Guides"
                                    :
                                    ""
                                }
                            </div>
                        </div>
                    </div>
                </div>
                <div className="row mt-4">
                    <div className="col-12">
                        <div 
                            onClick={()=>{
                                this.handleSelected(4);
                                history.push('/Admin_Blog');
                            }}
                            className={
                                this.state.selected === 4?
                                "admin-sidebar-option-active"
                                :"admin-sidebar-option"
                            }
                        >
                            <img
                                alt="blog"
                                className="admin-sidebar-option-icon"
                                src={require('../../Assets/Icons/BlogAction/blog.svg')} 
                            />
                            <div className="admin-sidebar-option-text">
                                {
                                    !this.props.collpase?
                                    "Blogs"
                                    :
                                    ""
                                }
                            </div>
                        </div>
                    </div>
                </div>
                <div className="row mt-4">
                    <div className="col-12">
                        <div 
                            onClick={()=>{
                                this.handleSelected(5);
                                history.push('/Admin_Contact');
                            }}
                            className={
                                this.state.selected === 5?
                                "admin-sidebar-option-active"
                                :"admin-sidebar-option"
                            }
                        >
                            <img
                                alt="blog"
                                className="admin-sidebar-option-icon"
                                src={require('../../Assets/Icons/BlogAction/blog.svg')} 
                            />
                            <div className="admin-sidebar-option-text">
                                {
                                    !this.props.collpase?
                                    "Contact Us"
                                    :
                                    ""
                                }
                            </div>
                        </div>
                    </div>
                </div>
                <div className="row mt-4">
                    <div className="col-12">
                        <div 
                            onClick={()=>{
                                this.handleSelected(6);
                                history.push('/Admin_Category');
                            }}
                            className={
                                this.state.selected === 6?
                                "admin-sidebar-option-active"
                                :"admin-sidebar-option"
                            }
                        >
                            <img
                                alt="blog"
                                className="admin-sidebar-option-icon"
                                src={require('../../Assets/Icons/BlogAction/blog.svg')} 
                            />
                            <div className="admin-sidebar-option-text">
                                {
                                    !this.props.collpase?
                                    "Categories"
                                    :
                                    ""
                                }
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </React.Fragment> );
    }
}
 
export default Sidebar;