import '../SignIn/signIn.css';
import './forgetPassword.css';
import React, { Component } from 'react';
import history from '../../../Routing/history';
import ColmnImage from "../../Assets/Icons/SignIn/NoPath - Copy (11).png";
import EmailIcon from "../../Assets/Icons/SignIn/Icon ionic-ios-mail.png";
import LockIcon from "../../Assets/Icons/SignIn/Icon awesome-lock.png";

class EnterPassword extends Component {
    state = {  }
    render() { 
        return ( <React.Fragment>
            <div className="container-fluid sign-in-container bg-white  h-100">
                <div className="row ">
                    <div className="col-xl-7 col-lg-6 col-md-d-none col-sm-none col-none mt-5 Image-colomn-padding">
                        <img 
                            // style={{position:"relative",top:"0",bottom:"0",left:"0",right:"0",margin:"auto"}} 
                            className="sign-in-cover-image"
                            src={ColmnImage} 
                            width="40%"
                            />
                    </div>
                    <div className="col-xl-5 col-lg-6 col-md-12 col-sm-12 col-12 p-5">
                    <div className="row mt-5 pt-5 pb-4">
                    <div className="col-xl-12 Sign-In-heading">
                        Forget Password
                    </div>
                </div>
                <div class="Sign-in-input-container-position sign-in-input-container">
                    <img src={LockIcon} className="mt-2"/>
                    <input type="password" class="Sign-IN-input-fields" placeholder="Password" />
                    </div>
                <div class="Sign-in-input-container-position sign-in-input-container">
                    <img src={LockIcon} className="mt-2"/>
                    <input type="password" class="Sign-IN-input-fields" placeholder="Confirm Passowrd" />
                    </div>
                    <div className="ml-5 pl-5">
                    <button 
                        onClick={()=>history.push('/Login')} 
                        className="sign-in-Register-button"
                    >
                        Done
                    </button>
                    </div>
                    
                    </div>

                </div>
                <div className="row">
                <div className="col-xl-8 col-lg-6 col-md-none col-sm-none col-none">

                </div>
                <div className="col-xl-4 col-lg-6 col-md-12 col-sm-12 col-12 mt-3 ">
                <p onClick={()=>history.push('/Signup')}><a className="not-have-an-account-text">Already have an account? Sign In</a></p>
                </div>
                </div>
            </div>
        </React.Fragment> );
    }
}
 
export default EnterPassword;