import "./sidebar.css";
import React, { Component } from "react";

import history from "../../../../Routing/history";

class Sidebar extends Component {
  state = {
    selected: this.props.selected ? this.props.selected : 1,
  };

  handleSelected = (SELECT) => {
    this.setState({
      selected: SELECT,
    });
  };
  render() {
    return (
      <React.Fragment>
        <div className="container-fluid p-0">
          <div
            onClick={() => history.push("/security-system/")}
            className="row cursor-pointer mt-3"
          >
            <div className="col-12 text-white">
              <img
                alt="back"
                style={{
                  width: "25px",
                  float: "left",
                  margin: "0 20px 0 30px",
                }}
                src={require("../../../Assets/Icons/BlogAction/left-arrow.svg")}
              />
              Back
            </div>
          </div>
          <div className="row mt-5">
            <div className="col-12">
              <div
                onClick={() => {
                  this.handleSelected(1);
                  history.push("/Blog_List");
                }}
                className={
                  this.state.selected === 1
                    ? "sidebar-option-active"
                    : "sidebar-option"
                }
              >
                <img
                  alt="blog"
                  className="sidebar-option-icon"
                  src={require("../../../Assets/Icons/BlogAction/blog.svg")}
                />
                <div className="sidebar-option-text">
                  {!this.props.collpase ? "BLOGS" : ""}
                </div>
              </div>
            </div>
          </div>
          <div className="row mt-5">
            <div className="col-12">
              <div
                onClick={() => {
                  this.handleSelected(2);
                  history.push("/Requested_Blog_List");
                }}
                className={
                  this.state.selected === 2
                    ? "sidebar-option-active"
                    : "sidebar-option"
                }
              >
                <img
                  alt="blog"
                  className="sidebar-option-icon"
                  src={require("../../../Assets/Icons/BlogAction/blog.svg")}
                />
                <div className="sidebar-option-text">
                  {!this.props.collpase ? "REQUESTED BLOGS" : ""}
                </div>
              </div>
            </div>
          </div>
        </div>
      </React.Fragment>
    );
  }
}

export default Sidebar;
