import './requestedBlogList.css';
import React, { Component } from 'react';
import history from '../../../../Routing/history';

class RequestedBlogTable extends Component {
    state = { 
        searchSelect: false
     }
     handleSearchSelect = () =>{
         this.setState({
             searchSelect: !this.state.searchSelect
         })
     }
    render() { 
        return ( <React.Fragment>
            <div className="container-fluid bg-white rounded-lg shadow-sm">
                <div className="row pt-2">
                    <div className="col-12 col-sm-12 col-md-5 col-lg-6 col-xl-6 blog-action-blog-list-title">
                        Requested Blogs List
                    </div>
                    <div className="col-12 col-sm-12 col-md-7 col-lg-6 col-xl-6">
                        <button onClick={()=>history.push('/Add_Blog')} className="btn blog-action-blog-list-add-blog-btn">
                            <img
                                alt="blog"
                                className="blog-action-blog-list-add-blog-icon"
                                src={require('../../../Assets/Icons/BlogAction/blog.svg')}
                            />
                            Add New Blog
                        </button>
                        {
                            this.state.searchSelect?
                            <div class="input-group blog-action-blog-list-search-input-group mb-3">
                              <input type="text" class="form-control blog-action-blog-list-search-input" placeholder="Search"/>
                              <div class="input-group-prepend">
                                <span onClick={()=>this.handleSearchSelect()} class="input-group-text blog-action-blog-list-search-append">X</span>
                              </div>
                            </div>
                            :
                            <img
                                alt="search"
                                onClick={()=>this.handleSearchSelect()}
                                className="blog-action-blog-list-search-icon"
                                src={require('../../../Assets/Icons/BlogAction/search.svg')}
                            />
                        }
                    </div>
                </div>
                <div className="row">
                    <div className="col-12 p-0">
                        <table className="table table-striped table-responsive-sm">
                            <thead className="bg-light">
                                <td className="text-center text-muted">Sr#</td>
                                <td className="text-center text-muted">Name</td>
                                <td className="text-center text-muted">Blog Date</td>
                                <td className="text-center text-muted">Action</td>
                            </thead>
                            <tbody>
                                <tr>
                                    <td className="text-center">Blog-0001</td>
                                    <td className="text-center pl-5 pr-5">John Vick</td>
                                    <td className="text-center">11-11-2020</td>
                                    <td className="text-center">
                                        <div className="blog-action-blog-list-table-option-box shadow-sm dropdown dropleft">
                                            <img
                                                alt="menu"
                                                style={{width:"20px", height:"20px"}}
                                                src={require('../../../Assets/Icons/BlogAction/dot.svg')}
                                                data-toggle="dropdown"
                                            />
                                            <div class="dropdown-menu">
                                              <a onClick={()=>history.push('/Edit_Blog')} class="dropdown-item text-center">View</a>
                                              <a class="dropdown-item text-center">Remove</a>
                                            </div>
                                        </div>
                                    </td>
                                </tr>
                                <tr>
                                    <td className="text-center">Blog-0001</td>
                                    <td className="text-center pl-5 pr-5">John Vick</td>
                                    <td className="text-center">11-11-2020</td>
                                    <td className="text-center">
                                        <div className="blog-action-blog-list-table-option-box shadow-sm dropdown dropleft">
                                            <img
                                                alt="menu"
                                                style={{width:"20px", height:"20px"}}
                                                src={require('../../../Assets/Icons/BlogAction/dot.svg')}
                                                data-toggle="dropdown"
                                            />
                                            <div class="dropdown-menu">
                                              <a onClick={()=>history.push('/Edit_Blog')} class="dropdown-item text-center">View</a>
                                              <a class="dropdown-item text-center">Remove</a>
                                            </div>
                                        </div>
                                    </td>
                                </tr>
                                <tr>
                                    <td className="text-center">Blog-0001</td>
                                    <td className="text-center pl-5 pr-5">John Vick</td>
                                    <td className="text-center">11-11-2020</td>
                                    <td className="text-center">
                                        <div className="blog-action-blog-list-table-option-box shadow-sm dropdown dropleft">
                                            <img
                                                alt="menu"
                                                style={{width:"20px", height:"20px"}}
                                                src={require('../../../Assets/Icons/BlogAction/dot.svg')}
                                                data-toggle="dropdown"
                                            />
                                            <div class="dropdown-menu">
                                              <a onClick={()=>history.push('/Edit_Blog')} class="dropdown-item text-center">View</a>
                                              <a class="dropdown-item text-center">Remove</a>
                                            </div>
                                        </div>
                                    </td>
                                </tr>
                            </tbody>
                        </table>
                    </div>
                </div>
            </div>
        </React.Fragment> );
    }
}
 
export default RequestedBlogTable;