import '../../Assets/Styles/common.css';
import './dashboardView.css';
import React, { Component } from 'react';

import Navbar from '../../Components/Navbar/navbar';
import Sidebar from '../../Components/Sidebar/sidebar';
import Card from '../../Components/Dashboard/card';
import LineChart from '../../Components/Dashboard/lineChart';
import BarChart from '../../Components/Dashboard/barChart';

class DashboardView extends Component {
    state = {  }
    render() { 
        return ( <React.Fragment>
            <div className="blog-action-view-bg">
                <div 
                    className={
                        !this.props.collpase?
                        "blog-action-view-sidebar-open"
                        :
                        "blog-action-view-sidebar-close"
                    }
                >
                    <Sidebar
                        collpase={this.props.collpase}
                        selected={1}
                    />
                </div>
                <div 
                    className={
                        !this.props.collpase?
                        "blog-action-view-body-open"
                        :
                        "blog-action-view-body-close"
                    }
                >
                    <div className="view-body-scroll">
                        <div className="blog-action-view-blue-bg">
                            <div className="container-fluid">
                                <div className="row">
                                    <div className="col-12">
                                        <Navbar
                                            handleCollpase={this.props.handleCollpase}
                                            title="Home"
                                            subTitle="Dashboard"
                                        />
                                    </div>
                                </div>
                                <div className="row mt-5">
                                    <div className="col-0 col-sm-0 col-md-0 col-lg-1 col-xl-1"/>
                                    <div className="col-12 col-sm-12 col-md-12 col-lg-10 col-xl-10 pl-4 pr-4">
                                        <div className="row">
                                            <div className="col-12">
                                                <div className="admin-dashboard-view-card-container">
                                                    <Card
                                                        title="TOTAL USERS"
                                                        amount="123,456"
                                                        interval="Since last month"
                                                        src={require('../../Assets/Icons/Dashboard/arrow.svg')}
                                                    />
                                                    <Card
                                                        title="NEW USERS"
                                                        amount="123,456"
                                                        interval="Since last month"
                                                        src={require('../../Assets/Icons/Dashboard/user.svg')}
                                                    />
                                                    <Card
                                                        title="TOTAL BLOGS"
                                                        amount="123,456"
                                                        interval="Since last month"
                                                        src={require('../../Assets/Icons/Dashboard/blog.svg')}
                                                    />
                                                    <Card
                                                        title="TOTAL GUIDES"
                                                        amount="123,456"
                                                        interval="Since last month"
                                                        src={require('../../Assets/Icons/Dashboard/blog.svg')}
                                                    />
                                                </div>
                                            </div>
                                        </div>
                                        <div className="row mt-5">
                                            <div className="col-12 col-sm-12 col-md-12 col-lg-7 col-xl-7">
                                                <LineChart/>
                                            </div>
                                            <div className="col-12 col-sm-12 col-md-12 col-lg-5 col-xl-5 admin-dashboard-margin">
                                                <BarChart/>
                                            </div>
                                        </div>
                                    </div>
                                    <div className="col-12 col-sm-0 col-md-0 col-lg-1 col-xl-1"/>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </React.Fragment> );
    }
}
 
export default DashboardView;