import './contactList.css';
import React, { Component } from 'react';
import history from '../../../Routing/history';

class ContactListTable extends Component {
    state = { 
        searchSelect: false
     }
     handleSearchSelect = () =>{
         this.setState({
             searchSelect: !this.state.searchSelect
         })
     }
    render() { 
        return ( <React.Fragment>
            <div className="container-fluid bg-white rounded-lg shadow-sm">
                <div className="row pt-2">
                    <div className="col-12 col-sm-12 col-md-5 col-lg-6 col-xl-6 admin-contact-list-title">
                        Contact Us
                    </div>
                    <div className="col-12 col-sm-12 col-md-7 col-lg-6 col-xl-6">
                        {/* <button 
                            onClick={()=>history.push('/Admin_Add_Blog')} 
                            className="btn admin-contact-list-add-blog-btn"
                        >
                            <img
                                alt="blog"
                                className="admin-contact-list-add-blog-icon"
                                src={require('../../../Assets/Icons/BlogAction/blog.svg')}
                            />
                            Add New Blog
                        </button> */}
                        {
                            this.state.searchSelect?
                            <div class="input-group admin-contact-list-search-input-group mb-3">
                              <input type="text" class="form-control admin-contact-list-search-input" placeholder="Search"/>
                              <div class="input-group-prepend">
                                <span onClick={()=>this.handleSearchSelect()} class="input-group-text admin-contact-list-search-append">X</span>
                              </div>
                            </div>
                            :
                            <img
                                alt="search"
                                onClick={()=>this.handleSearchSelect()}
                                className="admin-contact-list-search-icon"
                                src={require('../../../Assets/Icons/BlogAction/search.svg')}
                            />
                        }
                    </div>
                </div>
                <div className="row">
                    <div className="col-12 p-0">
                        <table className="table table-striped table-responsive-sm">
                            <thead className="bg-light">
                                <td className="text-center text-muted">Sr#</td>
                                <td className="text-center text-muted">Email</td>
                                <td className="text-center text-muted">Message</td>
                                <td className="text-center text-muted">Action</td>
                            </thead>
                            <tbody>
                                <tr>
                                    <td className="text-center">Contact-0001</td>
                                    <td className="text-center pl-5 pr-5">someone@example.com</td>
                                    <td className="text-center">Message or Query</td>
                                    <td className="text-center">
                                        <button className="btn admin-contact-list-table-action-resolve-btn">
                                            Resolve
                                        </button>
                                    </td>
                                </tr>
                                <tr>
                                    <td className="text-center">Contact-0001</td>
                                    <td className="text-center pl-5 pr-5">someone@example.com</td>
                                    <td className="text-center">Message or Query</td>
                                    <td className="text-center">
                                        <button className="btn admin-contact-list-table-action-resolve-btn">
                                            Resolve
                                        </button>
                                    </td>
                                </tr>
                                <tr>
                                    <td className="text-center">Contact-0001</td>
                                    <td className="text-center pl-5 pr-5">someone@example.com</td>
                                    <td className="text-center">Message or Query</td>
                                    <td className="text-center">
                                        <button className="btn admin-contact-list-table-action-resolve-btn">
                                            Resolve
                                        </button>
                                    </td>
                                </tr>
                            </tbody>
                        </table>
                    </div>
                </div>
            </div>
        </React.Fragment> );
    }
}
 
export default ContactListTable;