import './post.css';
import React, { Component } from 'react';

class CreatePost extends Component {
    state = {  }
    render() { 
        return ( <React.Fragment>
            <div className="card hackable-post-body-create-post-bg">
                <div className="card-header border-bottom-0 hackable-post-body-create-post-title">
                    Create Post
                </div>
                <div className="card-body">
                    <div className="container-fluid">
                        <div className="row">
                            <div className="col-12">
                                <textarea
                                    className="form-control hackable-post-body-create-post-input"
                                    placeholder="Write what's on your mind"
                                />
                            </div>
                        </div>
                    </div>
                </div>
                <div className="card-footer border-top-0 bg-white text-center">
                    <button className="btn hackable-post-body-create-post-btn">
                        POST
                    </button>
                </div>
            </div>
        </React.Fragment> );
    }
}
 
export default CreatePost;