import "./postView.css";
import React, { Component } from "react";

import Navbar from "../../Components/Navbar/navbar";
import SideOption from "../../Components/SideOption/sideOption";
import Post from "../../Components/Post/post";
import Footer from "../../Components/Footer/footer";

class PostView extends Component {
  state = {};
  render() {
    return (
      <React.Fragment>
        <div
          style={{ paddingTop: "60px" }}
          className="container-fluid post-view-bg"
        >
          <div className="row">
            <div className="w-100">
              <Navbar />
            </div>
          </div>
          <div className="row">
            <SideOption />
            <div className="col-0 col-sm-0 col-md-0 col-lg-1 col-xl-1" />
            <div className="col-12 col-sm-12 col-md-12 col-lg-10 col-xl-10 pt-5 pb-5">
              <Post />
            </div>
            <div className="col-0 col-sm-0 col-md-0 col-lg-1 col-xl-1" />
          </div>
          <div className="row">
            <div className="w-100">
              <Footer />
            </div>
          </div>
        </div>
      </React.Fragment>
    );
  }
}

export default PostView;
