import './navbar.css';
import React, { Component } from 'react';

import history from '../../../../Routing/history';

class Navbar extends Component {
    state = {  }
    render() { 
        return ( <React.Fragment>
            <div className="container-fluid">
                <div className="row mt-4">
                    <div className="col-12">
                        <img 
                            alt="menu"
                            onClick={()=>this.props.handleCollpase()}
                            className="blog-action-nav-menu-icon"
                            src={require('../../../Assets/Icons/BlogAction/menu.svg')}
                        />
                        <div className="blog-action-nav-title">
                            Blogs
                        </div>
                        <img 
                            alt="home"
                            className="blog-action-nav-home-icon"
                            src={require('../../../Assets/Icons/BlogAction/home.svg')}
                        />
                        <div className="blog-action-nav-sub-title">
                            Home
                        </div>
                        <div className="blog-action-nav-sub-title">
                            -
                        </div>
                        <div className="blog-action-nav-sub-title">
                            {this.props.title}
                        </div>
                        <div className="blog-action-nav-action-container">
                            <img 
                                alt="help"
                                className="blog-action-nav-action-box"
                                src={require('../../../Assets/Icons/BlogAction/help.svg')}
                            />
                            <img 
                                id="profile-pic"
                                alt="help"
                                className="blog-action-nav-action-box"
                                src={require('../../../Assets/Icons/BlogAction/profile.svg')}
                            />
                            <div onClick={()=>history.push('/')} className="blog-action-nav-logout-box">Logout</div>
                        </div>
                    </div>
                </div>
            </div>
        </React.Fragment> );
    }
}
 
export default Navbar;