import './sideOption.css';
import React, { Component } from 'react';

class SideOption extends Component {
    state = {  }
    render() { 
        return ( <React.Fragment>
            <div className="side-option-container">
                <div className="side-option-text">
                    Share Now
                </div>
                <div className="side-option-icon-circle">
                    <img
                        alt="fb"
                        className="side-option-icon-img"
                        src={require('../../Assets/Icons/SideOptions/facebook.svg')}
                    />
                </div>
                <div className="side-option-icon-circle">
                    <img
                        alt="fb"
                        className="side-option-icon-img"
                        src={require('../../Assets/Icons/SideOptions/twitter.svg')}
                    />
                </div>
                {/* <div className="side-option-icon-circle">
                    <img
                        alt="fb"
                        className="side-option-icon-img"
                        src={require('../../Assets/Icons/SideOptions/youtube.svg')}
                    />
                </div> */}
                <div className="side-option-icon-circle">
                    <img
                        alt="fb"
                        className="side-option-icon-img"
                        src={require('../../Assets/Icons/SideOptions/linkedin.svg')}
                    />
                </div>
            </div>
        </React.Fragment> );
    }
}
 
export default SideOption;