import React, { Component } from "react";
import history from "./history";
import { Route, Switch, Router } from "react-router-dom";

// User Views
import SignInView from "../User/Views/SignIn/signInView";
import SignUpView from "../User/Views/Signup/signupView";
import EnterEmailView from "../User/Views/ForgotPassword/EnterEmail/enterEmailView";
import EnterCodeView from "../User/Views/ForgotPassword/EnterCode/enterCodeView";
import EnterPasswordView from "../User/Views/ForgotPassword/EnterPassword/enterPasswordView";
import HomeView from "../User/Views/Home/homeView";
import PostView from "../User/Views/Post/postView";
import GuideView from "../User/Views/Guide/guideView";
import GuideDescriptionView from "../User/Views/GuideDescription/guideDescriptionView";
import BlogView from "../User/Views/Blog/blogView";
import BlogListView from "../User/Views/BlogAction/BlogList/blogListView";
import RequestedBlogListView from "../User/Views/BlogAction/RequestedBlog/requestedBlogView";
import EditBlogView from "../User/Views/BlogAction/EditBlog/editBlogView";
import AddBlogView from "../User/Views/BlogAction/AddBlog/addBlogView";
import HackableLoginView from "../User/Views/HackableModule/Login/loginView";
import HackablePostView from "../User/Views/HackableModule/Post/postView";

// Admin Views
import AdminDashboardView from "../Admin/Views/Dashboard/dashboardView";
import AdminRequestedBlogView from "../Admin/Views/RequestedBlog/requestedBlogView";
import AdminGuideListView from "../Admin/Views/GuideList/guideListView";
import AdminBlogListView from "../Admin/Views/BlogList/blogListView";
import AdminRequestedBlogDetailView from "../Admin/Views/RequestedBlogDetail/requestedBlogDetailView";
import AdminAddGuideView from "../Admin/Views/AddGuide/addGuideView";
import AdminGuideDetailView from "../Admin/Views/GuideDetail/guideDetailView";
import AdminAddBlogView from "../Admin/Views/AddBlog/addBlogView";
import AdminBlogDetailView from "../Admin/Views/BlogDetail/blogDetailView";
import AdminLoginView from "../Admin/Components/Login/login";
import AdminContactListView from "../Admin/Views/Contact/contactListView";
import AdminCategoryListView from "../Admin/Views/Category/categoryListView";

class Routing extends Component {
  state = {
    collpase: false,
  };

  handleCollpase = () => {
    this.setState({
      collpase: !this.state.collpase,
    });
  };

  render() {
    return (
      <React.Fragment>
        <Router history={history}>
          <Switch>
            <Route
              exact
              path="/security-system/"
              render={(props) => <HomeView {...props} />}
            />
            <Route
              path="/Login"
              render={(props) => <SignInView {...props} />}
            />
            <Route
              path="/Signup"
              render={(props) => <SignUpView {...props} />}
            />
            <Route
              path="/Forgrt/Enter_Email"
              render={(props) => <EnterEmailView {...props} />}
            />
            <Route
              path="/Forgrt/Enter_Code"
              render={(props) => <EnterCodeView {...props} />}
            />
            <Route
              path="/Forgrt/Enter_Password"
              render={(props) => <EnterPasswordView {...props} />}
            />
            <Route path="/Post" render={(props) => <PostView {...props} />} />
            <Route path="/Guide" render={(props) => <GuideView {...props} />} />
            <Route
              path="/Guide_Description"
              render={(props) => <GuideDescriptionView {...props} />}
            />
            <Route path="/Blog" render={(props) => <BlogView {...props} />} />
            <Route
              path="/Blog_List"
              render={(props) => (
                <BlogListView
                  collpase={this.state.collpase}
                  handleCollpase={this.handleCollpase}
                  {...props}
                />
              )}
            />
            <Route
              path="/Requested_Blog_List"
              render={(props) => (
                <RequestedBlogListView
                  collpase={this.state.collpase}
                  handleCollpase={this.handleCollpase}
                  {...props}
                />
              )}
            />
            <Route
              path="/Edit_Blog"
              render={(props) => (
                <EditBlogView
                  collpase={this.state.collpase}
                  handleCollpase={this.handleCollpase}
                  {...props}
                />
              )}
            />
            <Route
              path="/Add_Blog"
              render={(props) => (
                <AddBlogView
                  collpase={this.state.collpase}
                  handleCollpase={this.handleCollpase}
                  {...props}
                />
              )}
            />
            <Route
              path="/Hackable_login"
              render={(props) => (
                <HackableLoginView
                  collpase={this.state.collpase}
                  handleCollpase={this.handleCollpase}
                  {...props}
                />
              )}
            />
            <Route
              path="/Hackable_Post"
              render={(props) => (
                <HackablePostView
                  collpase={this.state.collpase}
                  handleCollpase={this.handleCollpase}
                  {...props}
                />
              )}
            />

            {/* Admin Routes */}
            <Route
              path="/Admin_Login"
              render={(props) => (
                <AdminLoginView
                  collpase={this.state.collpase}
                  handleCollpase={this.handleCollpase}
                  {...props}
                />
              )}
            />
            <Route
              path="/Admin_Dashboard"
              render={(props) => (
                <AdminDashboardView
                  collpase={this.state.collpase}
                  handleCollpase={this.handleCollpase}
                  {...props}
                />
              )}
            />
            <Route
              path="/Admin_Requested_Blog"
              render={(props) => (
                <AdminRequestedBlogView
                  collpase={this.state.collpase}
                  handleCollpase={this.handleCollpase}
                  {...props}
                />
              )}
            />
            <Route
              path="/Admin_Guide"
              render={(props) => (
                <AdminGuideListView
                  collpase={this.state.collpase}
                  handleCollpase={this.handleCollpase}
                  {...props}
                />
              )}
            />
            <Route
              path="/Admin_Blog"
              render={(props) => (
                <AdminBlogListView
                  collpase={this.state.collpase}
                  handleCollpase={this.handleCollpase}
                  {...props}
                />
              )}
            />
            <Route
              path="/Admin_Requested_Blog_Detail"
              render={(props) => (
                <AdminRequestedBlogDetailView
                  collpase={this.state.collpase}
                  handleCollpase={this.handleCollpase}
                  {...props}
                />
              )}
            />
            <Route
              path="/Admin_Add_Guide"
              render={(props) => (
                <AdminAddGuideView
                  collpase={this.state.collpase}
                  handleCollpase={this.handleCollpase}
                  {...props}
                />
              )}
            />
            <Route
              path="/Admin_Guide_Detail"
              render={(props) => (
                <AdminGuideDetailView
                  collpase={this.state.collpase}
                  handleCollpase={this.handleCollpase}
                  {...props}
                />
              )}
            />
            <Route
              path="/Admin_Add_Blog"
              render={(props) => (
                <AdminAddBlogView
                  collpase={this.state.collpase}
                  handleCollpase={this.handleCollpase}
                  {...props}
                />
              )}
            />
            <Route
              path="/Admin_Blog_Detail"
              render={(props) => (
                <AdminBlogDetailView
                  collpase={this.state.collpase}
                  handleCollpase={this.handleCollpase}
                  {...props}
                />
              )}
            />
            <Route
              path="/Admin_Contact"
              render={(props) => (
                <AdminContactListView
                  collpase={this.state.collpase}
                  handleCollpase={this.handleCollpase}
                  {...props}
                />
              )}
            />
            <Route
              path="/Admin_Category"
              render={(props) => (
                <AdminCategoryListView
                  collpase={this.state.collpase}
                  handleCollpase={this.handleCollpase}
                  {...props}
                />
              )}
            />
          </Switch>
        </Router>
      </React.Fragment>
    );
  }
}

export default Routing;
