import React, { Component } from 'react';
import "./login.css";
import history from '../../../Routing/history';
class Login extends Component {
    state = {  }
    render() { 
        console.log(this.props.collapse);
        return ( 
            <React.Fragment>
            <div className="container-fluid Login-page-background-image text-center">
                <div className="card-padding-div">
                <div className="card Login-Card-component">
                <h2 className="Admin-heading-text">Admin Login</h2>
                <p className="Admin-welcome-Back-text">Welcome back! Please login to continue.</p>
            <div class="Login-page-input-icons-div mt-n3"> 
                <img
                    alt="mail"
                    className="login-icon"
                    src={require('../../Assets/Icons/Login/mail.svg')}
                />
                <input class="Admin-Login-input-field" type="text" placeholder="Username or email"/> 
            </div> 
            <div class="Login-page-input-icons-div"> 
                <img
                    alt="mail"
                    className="login-icon"
                    src={require('../../Assets/Icons/Login/lock.svg')}
                />              
                <input class="Admin-Login-input-field" type="password" placeholder="Password"/> 
            </div>
            <label class="login-page-checkbox-container"><span className="Login-Remember-me-text">Remember Me</span>
            <input type="checkbox" />
            <span class="new-created-check-box"></span>
            </label>
            {/* <span className="Admin-forget-password-text">Forget password?</span> */}
            <div className="text-center">
            <button onClick={()=>history.push('/Admin_Dashboard')} className="Admin-Sign-In-button">Sign in</button>
            </div>
            </div>
            </div>
            </div>
            </React.Fragment>
         );
    }
}
 
export default Login;