import './guideDescription.css';
import React, { Component } from 'react';

class ProgressBar extends Component {
    state = {  }
    render() { 
        return ( <React.Fragment>
            <div className="container-fluid guide-description-body-progress-bar-bg">
                <div className="row">
                    <div className="col-0 col-sm-1 col-md-2 col-lg-3 col-xl-3"/>
                    <div className="col-12 col-sm-10 col-md-8 col-lg-6 col-xl-6">
                        <div className="row">
                            <div className="col-6 col-sm-6 col-md-5 col-lg-4 col-xl-4">
                                <div className="text-white float-left">
                                    PROGRESS
                                </div> 
                                <div className="">
                                    {this.props.progress}
                                </div>
                            </div>
                            <div className="col-6 col-sm-6 col-md-7 col-lg-8 col-xl-8">
                                <div class="progress guide-description-body-progress-bar-container">
                                    <div style={{width:this.props.progress? this.props.progress : "40%"}} class="progress-bar guide-description-body-progress-bar"></div>
                                </div>
                            </div>
                        </div>
                    </div>
                    <div className="col-0 col-sm-1 col-md-2 col-lg-3 col-xl-3"/>
                </div>
            </div>
        </React.Fragment> );
    }
}
 
export default ProgressBar;